export const getAllOrders = () => {
  return fetch(
    'https://sheet.best/api/sheets/21ee2682-daf0-47a2-9f81-9779f1ea7cb0',
    {
      method: 'GET',
    }
  ).then((response) => {
    return response.json();
  });
};

export const getOrderByDeliveryReference = (v) => {
  return getAllOrders(v).then((orders) => {
    return orders.find((order) => order['UNIQUE DELVIERY REFERENCE'] === v);
  });
};

export const getDeliveryAvailabilities = () => {
  return fetch(
    'https://sheet.best/api/sheets/b501f9c7-0b6c-4d8d-bfde-a953e55a7c64',
    {
      method: 'GET',
    }
  ).then((response) => {
    return response.json();
  });
};

export const updateDayDelivery = (deliveryReference, dayDelivery) => {
  return fetch(
    `https://sheet.best/api/sheets/21ee2682-daf0-47a2-9f81-9779f1ea7cb0/UNIQUE DELVIERY REFERENCE/${deliveryReference}`,
    {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        'CUSTOMER CONFIRMED DELIVERY SLOT': dayDelivery,
      }),
    }
  );
};

export const getDayAvailabilityForPostcode = (postcode) => {
  return getDeliveryAvailabilities().then((availabilities) => {
    let result = {};
    availabilities.forEach((row) => {
      result[row['Date (DD/MM/YY)']] = row[postcode] === 'TRUE';
    });
    return result;
  });
};
